.row_posters {
    display: flex;
    overflow-x: auto;
    padding: 20px 0;
    -ms-overflow-style: none;
    scrollbar-width: none;
}

.row_posters::-webkit-scrollbar {
    display: none;
}

.row_poster {
    max-height: 100px;
    object-fit: contain;
    margin-right: 10px;
    transition: transform 450ms;
    border-radius: 4px;
    cursor: pointer;
}

.row_poster:hover {
    transform: scale(1.08);
    opacity: 1;
}

.row_posterLarge {
    max-height: 250px;
}